* {
  box-sizing: border-box;
}
*::selection {
  background-color: transparent;
}
body {
  background-color: black;
  margin: 0;
  color: #fff;
  overflow: hidden;
  height: 100vh;
  font-family: "Roboto", sans-serif;
}

.app {
  height: 100vh;
  display: flex;
  justify-content: center;

  .playMusic {
    width: 60px;
    height: 60px;
    position: absolute;
    padding: 0;
    bottom: 50px;
    right: 50px;
    border-radius: 50%;
    background-color: rgb(0, 102, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s;
    cursor: pointer;
    span {
      font-size: 70px;
      transition: 0.2s;
    }
    &:hover {
      background-color: rgb(137, 184, 255);
    }
  }
  .office {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    animation: zoom 3s;
    .background {
      position: absolute;
      top: -350px;
      width: 100vw;
      height: 170vh;
      background-image: url(../images/floor.jpg);
      background-size: 40vw;
    }
    .chair {
      width: 25vw;
      min-width: 100px;
      background-size: cover;
      position: absolute;
      top: 30vw;
      transition: 0.7s transform;
      &:hover {
        transform: translate(20px, 100px) rotate(-20deg);
      }
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        box-shadow: 60px 20px 80px 11vw rgba(0, 0, 0, 0.644);
      }
      img {
        width: 100%;
        transform: rotate(180deg);
      }
      .secret-msg {
        position: absolute;
        font-size: 100%;
        top: 5%;
        left: 25%;
        width: 15%;
        height: 15%;
        background-color: rgb(37, 113, 255);
        transform: rotate(-10deg);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: 0.6s;
        &:hover {
          filter: brightness(1.3);
        }
      }

      .secret-on {
        transform: translate(50px, 450px) scale(0) rotate(360deg);
        &:hover {
          transition: 0.3s;
          z-index: 3;
          transform: translate(50px, 50px) scale(3) rotate(20deg);
        }
      }
    }
  }

  &::after {
    content: "";
    left: -450px;
    top: -450px;
    position: absolute;
    border-radius: 50%;
    box-shadow: 300px 300px 500px 400px rgba(255, 255, 255, 0.788);
    animation: light-change 3s infinite alternate;
  }

  @keyframes light-change {
    0% {
      box-shadow: 200px 200px 500px 300px rgba(255, 255, 255, 0.788);
    }
    100% {
      box-shadow: 450px 350px 500px 400px rgba(255, 255, 255, 0.788);
    }
  }
}

.background-darkening-floor {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.541),
    rgba(0, 0, 0, 0.801)
  );
}

// For all items on Table

.phone,
.note {
  .item-title {
    left: 50%;
    transform: translateX(-50%) scale(0.4);
    position: absolute;
    width: 100%;
    height: 150px;
    top: -50px;
    background-color: #fff;
    border-radius: 20px;
    transition: 0.6s;
    font-size: 500%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    color: #000;
    opacity: 0;
  }

  &.hover:hover {
    transform: scale(0.25) translate(20px, -40%) rotate(0deg);
    box-shadow: 200px 200px 80px rgba(0, 0, 0, 0.3);
    filter: brightness(1.2);
    z-index: 3;
    .item-title {
      opacity: 1;
      top: -200px;
      transform: translateX(-50%) scale(1);
    }
    .background-darkening-phone::after {
      content: "";
      z-index: 3;
      width: 1000px;
      height: 1px;
      transform: rotate(-45deg);
      box-shadow: 0 0 80px 10px #fff;
      position: absolute;
      animation: shine 2.5s infinite;
    }
  }

  &.scale {
    transition: 0.3s;
    box-shadow: 25px 20px 20px rgba(0, 0, 0, 0.6);
    top: calc(80% - 30vw);
    left: 60%;
    z-index: 1;
    cursor: pointer;
    .screen-content {
      filter: brightness(0.4);
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
    }
  }

  //  projects inside phone
  &.noScale {
    .project {
      .top {
        img:hover {
          transform: scale(1.05);
          filter: brightness(0.8);
        }
      }
    }
  }
  @keyframes hover-float-phone {
    0% {
      top: 400px;
    }
    50% {
      top: 410px;
    }
    100% {
      top: 400px;
    }
  }
  @keyframes hover-float-note {
    0% {
      top: 370px;
    }
    50% {
      top: 380px;
    }
    100% {
      top: 370px;
    }
  }
  @keyframes shine {
    0% {
      top: -20%;
      left: -300%;
    }
    60% {
      left: 100%;
      top: 100%;
    }
    100% {
      left: 100%;
      top: 100%;
    }
  }
}
@keyframes zoom {
  0% {
    transform: scale(0.6);
  }
  50% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 1024px) {
  .app {
    .office {
      .chair {
        .secret-msg {
          font-size: 50%;
        }
        &:hover {
          transform: translate(15px, 70px) rotate(-20deg);
        }
      }
      .table {
        animation: table-shadow-medium 3s infinite alternate;
        .monitor {
          margin-top: 40px;
          &::before {
            width: 250px;
            box-shadow: -80px 30px 45px 40px rgba(0, 0, 0, 0.644);
          }
        }
        .mouse {
          &::before {
            box-shadow: 3px 3px 5px 8px rgba(0, 0, 0, 0.644);
          }
        }
        .speaker {
          left: 90%;
          &::before {
            border-radius: 10px;
            width: 50%;
            top: 36%;
            left: 20%;
          }
        }
        .speaker-left {
          left: 10%;
          transform: rotate(-15deg) translateX(-50%);
        }
        .phone,
        .note {
          .item-title {
            padding: 0 30px;
            width: auto;
          }
          &.scale {
            .phone-inside {
              .top-curve {
                height: 10px;
                .camera {
                  width: 4px;
                  aspect-ratio: 1/1;
                }
                .microphone {
                  height: 5px;
                  width: 40%;
                }
              }
              .screen-content {
                height: 90%;
              }
            }
            .note-inside {
              font-size: 1vw;
              i {
                font-size: 20px;
              }
            }
          }
        }
        .phone {
          &.scale {
            border: 6px solid rgba(255, 255, 255, 0.733);
            border-radius: 20px;

            .phone-inside {
              color: rgba(255, 255, 255, 0);
              .screen-content {
                padding: 5px;
              }
              .projects {
                font-size: 10px;
                margin: 0;
              }
              .project {
                margin: 5px;
                h1 {
                  font-size: 10px;
                }
                a,
                p {
                  font-size: 6px;
                }
                a {
                  width: 6 55px;
                }
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .app {
    &::after {
      left: 300px;
      top: -450px;
    }
    .office {
      .chair {
        .secret-msg {
          font-size: 40%;
        }
        .secret-on {
          &:hover {
            transform: translate(20px, 30px) scale(3) rotate(20deg);
          }
        }
      }
      .table {
        animation: table-shadow-mobile 3s infinite alternate;
        .monitor {
          &::before {
            width: 200px;
            box-shadow: -50px 30px 30px 30px rgba(0, 0, 0, 0.644);
          }
        }
        .keyboard {
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
        }
        .mouse {
          &::before {
            box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.644);
          }
        }
        .speaker {
          &::before {
            border-radius: 5px;
            left: 5%;
          }
          .sound-wave {
            .circle {
              height: 30px;
            }
          }
        }
        .speaker-left {
          &::before {
            top: 30%;
            left: 15%;
          }
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .app {
    &::after {
      left: 200px;
      top: -450px;
    }

    .playMusic {
      width: 40px;
      height: 40px;
      span {
        font-size: 50px;
      }
    }
    .office {
      .chair {
        width: 30vw;
        top: 50% !important;
        left: 17% !important;
        &:hover {
          transform: translate(10px, 50px) rotate(-20deg);
        }
        .secret-msg {
          font-size: 40%;
        }
      }
      .table {
        width: 450px !important;
        height: 40vw;
        animation: table-shadow-mobile 3s infinite alternate;
        .monitor {
          &::before {
            width: 150px;
            box-shadow: -40px 25px 25px 20px rgba(0, 0, 0, 0.644);
          }
        }
        .keyboard {
          left: 35vw;
        }
        .mouse {
          right: 25vw;
        }
      }
    }
  }
}
@media (orientation: portrait) and (max-width: 1024px) and (min-width: 768px) {
  .app {
    &::after {
      left: 400px;
      top: -450px;
    }
  }
}
@media (orientation: portrait) {
  .app {
    .playMusic {
      transform: rotate(90deg);
      left: 20px;
      top: 20px;
    }
    .office {
      .chair {
        top: 50%;
        left: 20vw;
        transform: translate(20px, -50%) rotate(90deg);
        &:hover {
          transform: translate(-30px, -40%) rotate(70deg);
        }
      }
      .table {
        border-radius: 7px;
        width: 80vw;
        height: 40vw;
        top: 50%;
        transform: translate(10%, -70%) rotate(90deg);
        .background-darkening-table {
          border-radius: 7px;
        }
        .phone,
        .note {
          &.hover:hover {
            box-shadow: 100px 100px 60px rgba(0, 0, 0, 0.3);
          }
          &.scale {
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.6);
            top: calc(50% - 15vw);
          }
          &.noScale {
            width: 350px;
            top: 50%;
            left: 50%;
            transform: translate(-56%, -44%) rotate(-90deg);
            .note-inside {
              h1 {
                font-size: 20px;
              }
              p {
                font-size: 12px;
              }
            }
          }
        }
        .keyboard {
          left: 37%;
        }
        .mouse {
          right: 35%;
        }
      }
    }
  }
}
